class PageStackJSInterop {
  handle: DotNet.DotNetObject;

  constructor(handle: DotNet.DotNetObject) {
    this.handle = handle;
    document.addEventListener("click", this._onDocumentClick);
  }

  _onDocumentClick(event: MouseEvent) {
    const target = event.target as HTMLElement;
    if (target.tagName !== "A") return;

    const href = target.getAttribute("href");
    if (!href) return;

    let strategy = target.getAttribute("data-page-stack-strategy");
    strategy = strategy ? strategy.toLowerCase() : null;
    if (strategy === "" || strategy === "true" || strategy === "push") {
      this.handle.invokeMethodAsync("Push", href);
    } else if (strategy === "replace") {
      this.handle.invokeMethodAsync("replace", href);
    } else if (strategy === "popAndReplace") {
      this.handle.invokeMethodAsync("PopAndReplace", href);
    }
  }

  dispose() {
    document.removeEventListener("click", this._onDocumentClick);
  }
}

export function init(handle: DotNet.DotNetObject) {
  return new PageStackJSInterop(handle);
}
